import React, { PureComponent } from 'react'
import { Helmet } from "react-helmet";

export default class FindPlants extends PureComponent {
    render() {
        return (
            <div className="findplantspos above-area bg-24">
                <Helmet>
                    <title>Find Best Plants</title>
                </Helmet>
                <div className="d-flex text-white align-items-center justify-content-center h-100">
                    <h1>Find Your Best Plants Here</h1>
                </div>
            </div>
        )
    }
}
